
.hypertension{
	font-size: 0.28rem;
}
.hypertension .left{
	display: inline-block;
	vertical-align: text-top;
	text-align: center;
}
.hypertension .left .item.on{
	background: #3C9BFF;
	color: #fff;
}
.hypertension-content{
	display: inline-block;
	width: 83.2%;
	vertical-align: text-top;
	height: 100%;
	border-left: 1px solid #eee;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.content-head{
	padding: 15px 15px;
	border-bottom:1px solid #eee;
	color: #3C9BFF;
}
.content-head>div{
	width: 50%;
	display: inline-block;
}
.content-head-right{
	text-align: right;
}
.content-head-right img{
	width: .5rem;
	height: .5rem;
	vertical-align: middle;
	margin-right: 10px;
}
.content-head-right span{
	vertical-align: middle;
}
/*随访列表*/
.content-list{
	padding: 15px 15px;
}
.list-item{
	padding: 20px 15px;
	-webkit-box-shadow: 0 0 15px #ebf5ff;
	        box-shadow: 0 0 15px #ebf5ff;
	margin-bottom: 10px;
}
.list-item>div{
	display: inline-block;
	vertical-align: middle;
}
.list-item-left{
	width:35%;
}
.list-item-center{
	width:45%;
}
.list-item-right{
	width:20%;
}
.list-item-right span{
	background: #3C9BFF;
	color: #fff;
	padding: 8px 30px;
	display: inline-block;
	border-radius: 5px;
}
